.text50{
  font-size: 3.125em;
  line-height: 129%;
}

.text22{
  font-size: 1.375em;
  line-height: 118%;
}

.text20{
  font-size: 1.25em;
  line-height: 145%;
}

.text p,
.text li,
.text18{
  font-size: 1.125em;
  line-height: 167%;
}

.text16{
  font-size: 1em;
  line-height: 162%;
}

.text12{
  font-size: .75em;
  line-height: 167%;
}

.light-text{
  font-weight: 300;
}

.medium-text{
  font-weight: 500;
}

.dark-gray-text{
  color: $darkGray;
}

.primary-text{
  color: $primary;
}

.text{
  ul{
    @include grid100gap(1.125em);
    li{
      position: relative;
      padding-left: 1.55em;
      &:before{
        content: '';
        position: absolute;
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
        width: .89em;
        height: .89em;
        top: .28em;
        left: 0;
      }
    }
  }
}

.text22-desktop{
  @media screen and (min-width: 993px){
    font-size: 1.375em;
  }
}


@media screen and (max-width: 992px){
  .text50{
    font-size: 1.375em;
    line-height: 136%;
  }

  .text22{
    font-size: .875em;
    line-height: 171%;
  }

  .text p,
  .text li,
  .text20,
  .text18{
    font-size: .75em;
  }

  .text12{
    font-size: .625em;
  }

  .text{
    ul{
      gap: .625em;
      li{
        padding-left: 2.17em;
        &:before{
          width: 1.16em;
          height: 1.16em;
          top: .35em;
        }
      }
    }
  }
}
