@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.back-btn{
  z-index: 4;
  position: absolute;
  right: 11em;
  bottom: 9em;
  width: 4.625em;
  height: 4.625em;
  border-radius: 50%;
  border: 1px solid #fff;
  @include center;
  flex-direction: column;
  &:hover{
    color: #fff;
    opacity: .75;
  }
  svg{
    width: .8125em;
    height: auto;
    margin-bottom: .25em;
  }
  &._blue{
    border-color: $primary;
    &:hover{
      color: $black;
    }
  }
}


@media screen and (max-width: 1750px){
  .back-btn{
    right: 2em;
    bottom: 5em;
  }
}


@media screen and (max-width: 992px){
  .back-btn{
    width: 2.5em;
    height: 2.5em;
    right: .94em;
    bottom: .94em;
    &._blue{
      background-color: $primary;
      color: #fff;
      path{
        stroke: #fff;
      }
      &:hover{
        color: #fff;
      }
    }
    svg{
      width: .625em;
      margin-bottom: .125em;
    }
    span{
      font-size: .5em;
    }
  }
}
