.main{
  position: relative;
  &__video{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    video{
      width: 100%;
      height: 100%;
      object-fit: cover;
      &::-webkit-media-controls-panel {
        display: none!important;
        -webkit-appearance: none;
      }
      &::-webkit-media-controls-play-button {
        display: none!important;
        -webkit-appearance: none;
      }
      &::-webkit-media-controls-start-playback-button {
        display: none!important;
        -webkit-appearance: none;
      }
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding: 10.12em 0 6.19em;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__title{
    display: flex;
    flex-direction: column;
    font-size: 5.625em;
    line-height: 94%;
    text-transform: uppercase;
    font-weight: 300;
    & > * {
      line-height: inherit;
      &:first-letter{
        color: $primary;
      }
    }
  }
  &__text{
    max-width: 39em;
    margin-top: 1.875em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
}

.main-quote{
  z-index: 2;
  position: absolute;
  right: 6em;
  bottom: 6em;
  //width: 31.875em;
  padding: .625em .625em .625em 1.875em;
  border-radius: 0 1.875em 1.875em 1.875em;
  border: 1px solid $primary;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.20) 0%, rgba(255, 255, 255, 0.06) 100%);
  backdrop-filter: blur(24.46637535095215px);
  display: flex;
  align-items: center;
  &__content{
    @include grid100gap(.25em);
  }
  &__img{
    width: 3.5625em;
    height: 3.5625em;
    flex-shrink: 0;
    margin-left: .875em;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: block;
      object-fit: cover;
    }
  }
  &__icon{
    width: 2.3125em;
    position: absolute;
    top: -1.19em;
    left: -1.19em;
  }
}

.contacts{
  padding: 80px 0 40px;
  &__inner{
    display: grid;
    grid-template-columns: 28.625em calc(100% - 29.25em);
    gap: .625em;
  }
  &__items{
    @include grid100gap(1.875em);
  }
}

.contacts-item{
  max-width: 38.25em;
  @include grid100gap(.9375em);
  &__desc{
    font-size: 2.375em;
    line-height: 105%;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    a{
      line-height: 132%;
    }
  }
}

.why{
  padding: 5em 0;
  background-color: $primary;
  .text50{
    color: #fff;
  }
  &__grid{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.13em;
  }
}

.why-card{
  padding: 1.875em;
  border-radius: 1em;
  border: 1px solid #EFF3F3;
  background-color: #fff;
  &__icon{
    display: block;
    height: 1.375em;
    margin-bottom: 1.25em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__text{
    margin-top: 1.25em;
  }
}

.investment{
  padding: 5em 0;
  position: relative;
  &__top{
    display: grid;
    grid-template-columns: 38.875em calc(100% - 38.875em);
    .text{
      margin-top: 1.875em;
      max-width: 34em;
    }
  }
  &__content{
    max-width: 37em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 1.125em;
    @media screen and (min-width: 993px){
      ul{
        display: block;
        column-count: 2;
        column-gap: 1.875em;
      }
      li{
        display: inline-block;
        width: 100%;
        @include lastM(0 0 1em);
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: .75em;
  }
}

.risk{
  background-color: $gray;
  padding: 5em 0;
  position: relative;
  &__top{
    position: relative;
    display: grid;
    grid-template-columns: 49.5em calc(100% - 49.5em);
    .text{
      margin-top: 1.56em;
      max-width: 45.5em;
    }
  }
  &__img{
    position: absolute;
    right: -3.12em;
    width: 29.9375em;
    top: -4.06em;
    img{
      display: block;
      width: 100%;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
    position: relative;
    z-index: 2;
  }
  .why__grid{
    margin-top: 1.06em;
    position: relative;
    z-index: 2;
  }
  &__bg{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 46.5em;
    pointer-events: none;
  }
}

.strategy,
.msfo{
  padding: 5em 0;
  overflow: hidden;
}

.msfo{
  position: relative;
  &__grid{
    display: grid;
    grid-template-columns: 38.3125em calc(100% - 40.9625em);
    gap: 2.65em;
  }
  .text{
    margin-top: 1.56em;
    max-width: 36em;
    ul{
      margin-top: 1.56em;
      gap: .56em;
    }
  }
  &__nav{
    margin-top: 1.81em;
    display: flex;
  }
  &__img{
    width: 49em;
    position: relative;
    img{
      position: absolute;
      top: -12em;
      left: 0;
      width: 100%;
      display: block;
    }
  }
}

.operation{
  padding: 5em 0;
  background-color: $primary;
  color: #fff;
  position: relative;
  z-index: 2;
  &__top{
    display: grid;
    grid-template-columns: 44.875em calc(100% - 45.685em);
    justify-content: space-between;
    .text{
      margin-top: 1.56em;
      max-width: 42em;
    }
  }
  &__img{
    width: 37.3125em;
    margin-top: -7.125em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__grid{
    margin-top: .3125em;
    display: grid;
    grid-template-columns: calc(100% - 54.125em) 13.9375em 13.25em 17.5625em;
    gap: 3.125em;
  }
}

.operation-item{
  &__img{
    height: 9.25em;
    margin-bottom: .56em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.strategy{
  position: relative;
  &__top{
    display: grid;
    grid-template-columns: 41em calc(100% - 41em);
    .text{
      margin-top: 1.31em;
    }
  }
  &__img{
    margin-top: -5.125em;
    margin-left: -4em;
    img{
      height: 21.3125em;
      display: block;
    }
  }
  &__bottom{
    max-width: 55em;
    ul{
      gap: .875em;
    }
  }
  @media screen and (min-width: 993px){
    .why__grid{
      margin-top: 3.75em;
      grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    }
    .why-card{
      padding: 1.5em;
    }
  }
}

.offer{
  padding: 5em 0;
  background-color: $gray;
  backdrop-filter: blur(24.46637535095215px);
  position: relative;
  &__bg{
    position: absolute;
    top: 0;
    left: 10.19em;
    width: 32.9375em;
  }
  &__grid,
  .text50{
    position: relative;
    z-index: 2;
  }
  &__grid{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.875em;
    width: calc(100% + 1.25em);
  }
}

.offer-card{
  min-height: 17.0625em;
  width: calc(100% / 3 - 1.25em);
  margin: 1.25em 1.25em 0 0;
  padding: 1.875em 1.875em .25em;
  border-radius: 1em;
  background-color: #fff;
  position: relative;
  &__img{
    margin-top: .75em;
    max-height: 12em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__btn{
    display: none;
  }
  &._big{
    width: calc(50% - 1.25em);
    @media screen and (min-width: 577px){
      .offer-card__top{
        max-width: 12.18em;
      }
      .offer-card__img{
        position: absolute;
        height: 100%;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        padding: 1em .37em;
        width: 28.375em;
        img{
          width: 100%;
          max-height: unset;
        }
      }
    }
  }
  &._active{
    .offer-card__info{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__info{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    border-radius: inherit;
    background-color: $primary;
    padding: 1.875em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .text22{
      font-weight: 300;
    }
    .text16{
      margin: 1.25em 0;
    }
  }
  &__nav{
    display: flex;
  }
  &__btn2{
    padding: 0 1.875em;
    height: 2.6875em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    font-size: 1.875em;
    padding: 0 0 1.875em;
    &__inner{
      padding: 6.5em 0 1em;
      background-image: url(../img/main-bg.webp);
      background-size: auto 100%;
      background-position: center;
    }
    &__title{
      font-size: 3.125em;
      line-height: 100%;
    }
    &__nav,
    &__text{
      margin-top: .94em;
    }
    &__text{
      max-width: 19em;
    }
    &__video{
      display: none;
    }
  }

  .main-quote{
    position: relative;
    bottom: unset;
    right: unset;
    margin: .81em .94em 0 auto;
    padding-left: 1.25em;
    width: 17em;
    border-radius: 0 1.25em 1.25em 1.25em;
    .text18{
      line-height: 125%;
    }
    .text12{
      line-height: 140%;
    }
    &__img{
      width: 2.5em;
      height: 2.5em;
      align-self: flex-start;
    }
    &__icon{
      width: 1.625em;
      top: -.8em;
      left: -.8em;
    }
  }

  .contacts{
    padding: .9375em 0 1.25em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      gap: .625em;
    }
  }

  .contacts-item{
    gap: 0;
    .text12{
      line-height: 170%;
    }
    &__desc{
      font-size: 1.125em;
      line-height: 167%;
      margin-top: -.11em;
      a{
        line-height: 167%;
      }
    }
  }

  .why{
    padding: 1.25em 0;
    &__grid{
      margin-top: .625em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .3125em;
    }
    &__nav{
      margin-top: .625em;
      justify-content: unset;
    }
  }

  .why-card{
    padding: 1em;
    border-radius: .5em;
    &__top{
      display: flex;
      align-items: center;
    }
    &__icon{
      height: auto;
      width: .875em;
      margin: 0 .625em 0 0;
      flex-shrink: 0;
    }
    &__text{
      margin-top: .625em;
    }
  }

  .investment,
  .risk{
    &__top{
      grid-template-columns: 100%;
      gap: .625em;
      .text{
        margin-top: .625em;
      }
    }
    &__img{
      order: -1;
      max-width: 25em;
    }
  }

  .investment{
    padding: 1.56em 0;
    .back-btn{
      bottom: 1.56em;
    }
    &__nav,
    &__bottom{
      margin-top: .625em;
    }
  }

  .risk{
    padding: 1.25em 0;
    .back-btn{
      bottom: 1.25em;
    }
    &__bg{
      display: none;
    }
    &__img{
      position: static;
      width: 100%;
    }
    &__nav{
      justify-content: unset;
    }
    .why__nav{
      margin-top: .625em;
    }
    .why-card{
      padding: .75em;
      &__top{
        align-items: flex-start;
      }
    }
  }

  .msfo{
    padding: 1em 0 2.1875em;
    .back-btn{
      bottom: 2.1875em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__img{
      order: -1;
      padding-bottom: 81.7%;
      width: 100%;
      max-width: 25em;
      @media screen and (min-width: 577px){
        padding-bottom: 50%;
      }
      img{
        width: 130%;
        top: 50%;
        transform: translateY(-55%);
      }
    }
    .text{
      margin-top: .625em;
      ul{
        margin-top: .625em;
      }
    }
    &__nav{
      margin-top: .625em;
    }
  }

  .operation{
    padding: 1.875em 0;
    .back-btn{
      bottom: 1.875em;
    }
    &__top{
      grid-template-columns: 100%;
      gap: .625em;
      .text{
        margin-top: .625em;
      }
    }
    &__img{
      max-width: 25em;
      order: -1;
      margin: 0;
      width: 100%;
    }
    &__grid{
      margin-top: .625em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .strategy{
    padding: 1.69em 0 .94em;
    &__top{
      grid-template-columns: 100%;
      gap: .625em;
      .text{
        margin-top: .625em;
      }
    }
    &__img{
      margin: 0;
      order: -1;
      max-width: 25em;
      img{
        width: 100%;
        height: auto;
      }
    }
    &__bottom{
      margin-top: .625em;
      ul{
        gap: .625em;
      }
    }
    .why-card{
      padding: .75em;
      &__top{
        align-items: flex-start;
      }
    }
  }

  .offer{
    padding: 1.25em 0;
    &__bg{
      display: none;
    }
    &__grid{
      margin-top: 0;
      width: calc(100% + .625em);
    }
  }

  .offer-card{
    padding: 1em;
    min-height: 11em;
    margin: .625em .625em 0 0;
    width: calc(100% / 3 - .625em);
    &__top{
      margin-bottom: 1.25em;
    }
    &__info{
      display: none;
    }
    &__img{
      max-height: 6em;
    }
    .text22{
      line-height: 135%;
    }
    &._big {
      width: calc(50% - .625em);
      @media screen and (min-width: 577px){
        .offer-card__img{
          width: 14em;
        }
        .offer-card__top{
          max-width: 8.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    font-size: 1em;
  }

  .main-quote{
    margin-left: 1.75em;
  }

  .why{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .operation{
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .offer{
    &__grid{
      display: grid;
      grid-template-columns: 100%;
      gap: .625em;
      width: 100%;
      margin-top: .625em;
    }
  }

  .offer-card{
    width: 100% !important;
    margin: 0;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__btn{
      width: 1.9375em;
      height: 1.9375em;
      border-radius: 50%;
      background-color: $gray;
      @include center;
      margin-left: 2em;
      flex-shrink: 0;
      svg{
        width: .75em;
        height: auto;
      }
    }
    &__img{
      margin-top: 0;
      img{
        max-height: 7.875em;
      }
    }
  }
}
