.header,
.footer{
  &__logo{
    flex-shrink: 0;
    display: flex;
    align-items: center;
    width: 3.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1.875em 0;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .container{
    max-width: 100%;
  }
}

.lang{
  display: flex;
  align-items: center;
  &__item{
    margin-left: 1.75em;
    font-size: 1em;
    line-height: 143%;
    font-weight: 600;
    font-family: $font2;
    text-transform: uppercase;
    color: rgba($black,.3);
    transition: all .3s;
    cursor: pointer;
    &:hover,
    &._active{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .625em 0;
  }

  .lang{
    &__item{
      font-size: .875em;
      margin-left: 1.14em;
    }
  }
}
