.footer{
  &__inner{
    border-top: 1px solid #EFF3F3;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.875em 0;
  }
  &__left{
    display: flex;
    align-items: center;
  }
  &__logo{
    margin-right: .875em;
  }
  &__conf{
    text-decoration: underline;
  }
  &__by{
    padding-right: 3.75em;
    position: relative;
    img{
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      height: .625em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      padding: 1.25em 0;
    }
    .text12{
      font-size: .75em;
    }
  }
}

@media screen and (max-width: 576px){
  .footer{
    &__inner{
      border-top: none;
      padding-top: 2.1875em;
      flex-direction: column;
    }
    &__left{
      flex-direction: column;
      margin-bottom: .875em;
    }
    &__logo{
      margin: 0 0 .875em;
    }
    &__by{
      margin-top: .875em;
    }
  }
}
