@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 4.3125em;
    border-radius: 18.75em;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-blue{
    box-shadow: 0 .125em .25em 0 rgba(5, 73, 223, 0.02), 0 .625em 2.5em 0 rgba(5, 73, 223, 0.05), 0 .125em .25em 0 rgba(5, 73, 223, 0.02), 0 .625em 2.5em 0 rgba(5, 73, 223, 0.05);
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,15%);
        color: #fff;
    }
}

.m-btn-white{
    box-shadow: 0 .125em .25em 0 rgba(5, 73, 223, 0.02), 0 .625em 2.5em 0 rgba(5, 73, 223, 0.05), 0 .125em .25em 0 rgba(5, 73, 223, 0.02), 0 .625em 2.5em 0 rgba(5, 73, 223, 0.05);
    background-color: #FFF;
    color: $primary;
    &:hover{
        background-color: darken(#FFF,15%);
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.5em;
        span{
            font-size: .875em;
        }
    }
}
